import React from "react"
import { Link, graphql, useStaticQuery } from "gatsby"

const BlogPage = () => {
  const data = useStaticQuery(graphql`
        query {            
          allMdx(sort: { fields: [frontmatter___date], order: DESC }) {
              nodes {
                  fields {
                      slug
                  }
                  frontmatter {
                      date(formatString: "YYYY-MM-DD")
                      title
                      description  
                      tags                      
                      featuredImage {                                           
                        publicURL
                      }           
                  }              
              }
            }
        }
    `)

  const posts = data.allMdx.nodes
  const gruppedPosts = {}
  posts.forEach(f => {
    let key = "Plany"
    if (f.frontmatter.date !== undefined && f.frontmatter.date !== null) {
      key = new Date(f.frontmatter.date).getFullYear()
    }
    if (gruppedPosts[key] === undefined) {
      gruppedPosts[key] = [f]
    }
    else {
      gruppedPosts[key].push(f)
    }
  })

  return (
    Object.keys(gruppedPosts).sort().reverse().map(year => {
      return (
        <div>
          <h2>{year}</h2>
          <ol class="post-card-box clearfix">
            {gruppedPosts[year].map(post => {
              const title = post.frontmatter.title || post.fields.slug
              const featuredImage = post.frontmatter.featuredImage?.publicURL
              const tags = post.frontmatter.tags
              const date = post.frontmatter.date

              return (
                <li>
                  <div class="post-card">
                    <Link to={post.fields.slug} class="post-card-image" style={{ backgroundImage: `url(${featuredImage})` }}>
                    </Link>
                    <div class="post-card-body">
                      {tags?.map(tag => { return (<Link to={"/tags#" + tag} class="tag">|&#32;{tag}</Link>) })}
                      <Link to={post.fields.slug} class="post-card-link">
                        <h3 class="post-card-title">{title} {date !== null && "(" + date + ")"} </h3>
                      </Link>
                    </div>
                  </div>
                </li>
              )
            })}
          </ol>
        </div>
      )
    })
  )
}


export default BlogPage 